'use strict'

import React from 'react/addons'

const { addons: { CSSTransitionGroup: ReactCSSTransitionGroup } } = React

const Flyout = React.createClass({
  displayName: 'Flyout',

  propTypes: {
    children: React.PropTypes.node,
    key: React.PropTypes.string.isRequired
  },

  getDefaultProps () {
    return {
      children: null
    }
  },

  render () {
    return (
      <ReactCSSTransitionGroup transitionName='flyout'>
        <div className='flyout' key={this.props.key}>
          {this.props.children}
        </div>
      </ReactCSSTransitionGroup>
    )
  }
})

export default Flyout



